import Vue from 'vue';
import Vuex from 'vuex';
import movieList from './modules/movie-list';
import movieDetail from './modules/movie-detail';
Vue.use(Vuex);

// 创建仓库
const store = new Vuex.Store({
  modules: {
    movieList,
    movieDetail
  },
  state: {},
  getters: {},
  mutations: {},
  actions: {}
});

export default store;



